<template>
    <el-row>
      <el-col :span="23">
          <div class="title"></div>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px">
              <el-form-item label="">
                <el-col :span="24">
                  <el-input v-model="ruleForm.time" :disabled=true></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label="" prop="alias">
                  <el-input v-model="ruleForm.alias" ></el-input>
              </el-form-item>
              <el-form-item label="">
                  <el-input v-model="ruleForm.name" :disabled=true></el-input>
              </el-form-item>
              <el-form-item label="" prop="section">
                  <el-col :span="24">
                    <el-input v-model="ruleForm.section"></el-input> 
                  </el-col>
              </el-form-item>
              
              <el-form-item label="" prop="post">
                <el-col :span="24">
                  <el-input v-model="ruleForm.post"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label="">
                <el-col :span="24">
                  <el-input v-model="ruleForm.type" :disabled=true></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label="" prop="type2">
                <el-col :span="24">
                  <el-select v-model="ruleForm.type2" placeholder="" size="small" >
                      <el-option
                        v-for="item in managerType"
                        :key="item.key"
                        :label="item.value"
                        :value="item.key">
                      </el-option>
                    </el-select>
                </el-col>
              </el-form-item>

              <el-form-item label="">
                <el-col :span="24">
                  <el-input v-model="ruleForm.state" :disabled=true></el-input>
                </el-col>
              </el-form-item>

              <el-form-item label="">
                <el-col :span="24">
                  <el-input v-model="ruleForm.author" :disabled=true></el-input>
                </el-col>
              </el-form-item>
              <el-form-item>
                  <!-- <el-col :span="12"> -->
                    <el-button type="primary" @click="submitForm('ruleForm')" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
                    <el-button type="primary" @click="modifyForm('ruleForm')" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
                    <el-button type="primary" @click="unRegisterForm('ruleForm')" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
                    <el-button type="primary" @click="registerForm('ruleForm')" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
                    <el-button type="primary" @click="initForm('ruleForm')" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
                  <!-- </el-col> -->
              </el-form-item>
          </el-form>
      </el-col>
    </el-row>
</template>

<script>
import axios from "axios";
import store from "@/vuex/store";
export default {
  name: "modifyProject",
  data() {
    var checkName = (rule,value,callback)=>{
      if(value == ''){
        callback(new Error(''));
      }else{
        var reg= /^[A-Za-z]+$/;
        if (reg.test(value)) //
        {
          callback();
        }else{
          callback(new Error('110'));
        }
      }
    };

    return {
      fullscreenLoading:false,
      managerType:store.state.managerType,
      ruleForm: {
        time: '',
        alias: '',
        name: '',
        section: '',
        post: '',
        type:'',
        type2:'',
        state:'',
        author:''
      },
      rules: {
        alias: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 100, message: " 2  5 ", trigger: "blur" }
        ],
        name: [
          { required:true,validator:checkName, trigger: "blur" }
        ],
        section: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 20, message: " 2  20 ", trigger: "blur" }
        ],
        post:[
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 20, message: " 2  20 ", trigger: "blur" }
        ],
        author:[
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 20, message: " 2  20 ", trigger: "blur" }
        ],
        type2:[
          { required: true, message: "", trigger: "blur" },
        ]
      },
    };
  },
  methods: {
    submitForm() {
      this.$router.go(-1);
    },
    modifyForm(){
      this.fullscreenLoading = true;
      this.$refs.ruleForm.validate(valid => { 
        if (valid) { 
          this.fullscreenLoading = true;
          setTimeout(() => {
            this.fullscreenLoading = true;
            this.$axios.post("/modifyManager.action",{
              name:this.ruleForm.name,
              alias:this.ruleForm.alias,
              section:this.ruleForm.section,
              post:this.ruleForm.post,
              type:this.ruleForm.type2
            })
            .then(response=>{
              if(response.data=='999999'){
                this.$message({
                  type: 'warning',
                  message: '!'
                }); 
                this.$router.push('/');
                return;
              }
              let errorcode = response.data.head.errorCode;
              if(errorcode != '000000'){
                let errorMessage = response.data.head.errorMessage;
                this.$message({
                  message: errorMessage,
                  type: 'error'
                });
                return;
              }
              this.$message({
                  message: '',
                  type: 'success'
              });
              this.$router.push('/container/queryManager');
              return;
            })
            .catch(error=>{
              this.$message({
                  message: '',
                  type: 'error'
              });
              fullscreenLoading:false;
              return;
            });
            
          }, 2000);
        } else { 
          console.log('submit err');
          this.$message({
              message: '',
              type: 'error'
          });
          this.fullscreenLoading = false;
          return;
        } 
      }) 
    },
    initForm(){
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = true;
        this.$axios.post("/initPassword.action",{
          name:this.ruleForm.name,
        })
        .then(response=>{
          if(response.data=='999999'){
            this.$message({
              type: 'warning',
              message: '!'
            }); 
            this.$router.push('/');
            return;
          }
          let errorcode = response.data.head.errorCode;
          if(errorcode != '000000'){
            let errorMessage = response.data.head.errorMessage;
            this.$message({
              message: errorMessage,
              type: 'error'
            });
            this.fullscreenLoading=false;
            return;
          }
          this.$message({
              message: '',
              type: 'success'
          });
          this.$router.push('/container/queryManager');
          return;
        })
        .catch(error=>{
          this.$message({
              message: '',
              type: 'error'
          });
          this.fullscreenLoading=false;
          return;
        });
        
      }, 2000);
    },
    unRegisterForm(){
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = true;
        this.$axios.post("/unRegisterManager.action",{
          name:this.ruleForm.name,
        })
        .then(response=>{
          if(response.data=='999999'){
            this.$message({
              type: 'warning',
              message: '!'
            }); 
            this.$router.push('/');
            return;
          }
          let errorcode = response.data.head.errorCode;
          if(errorcode != '000000'){
            let errorMessage = response.data.head.errorMessage;
            this.$message({
              message: errorMessage,
              type: 'error'
            });
            this.fullscreenLoading=false;
            return;
          }
          this.$message({
              message: '',
              type: 'success'
          });
          this.$router.push('/container/queryManager');
          return;
        })
        .catch(error=>{
          this.$message({
              message: '',
              type: 'error'
          });
          this.fullscreenLoading=false;
          return;
        });
        
      }, 2000);
    },
    registerForm(){
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = true;
        this.$axios.post("/initPassword.action",{
          name:this.ruleForm.name,
        })
        .then(response=>{
          if(response.data=='999999'){
            this.$message({
              type: 'warning',
              message: '!'
            }); 
            this.$router.push('/');
            return;
          }
          let errorcode = response.data.head.errorCode;
          if(errorcode != '000000'){
            let errorMessage = response.data.head.errorMessage;
            this.$message({
              message: errorMessage,
              type: 'error'
            });
            this.fullscreenLoading=false;
            return;
          }
          this.$message({
              message: '',
              type: 'success'
          });
          this.$router.push('/container/queryManager');
          return;
        })
        .catch(error=>{
          this.$message({
              message: '',
              type: 'error'
          });
          this.fullscreenLoading=false;
          return;
        });
        
      }, 2000);
    }
  },
  mounted:function(){
    this.ruleForm.time = this.$route.params.time;
    this.ruleForm.alias = this.$route.params.alias;
    this.ruleForm.name = this.$route.params.name;
    this.ruleForm.section = this.$route.params.section;
    this.ruleForm.post = this.$route.params.post;
    this.ruleForm.type = this.$route.params.type;
    this.ruleForm.state = this.$route.params.state;
    this.ruleForm.author = this.$route.params.author;
  }
};
</script>

<style scoped>
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
}

</style>



